.backdrop {
    position        : fixed;
    top             : 0;
    left            : 0;
    width           : 100%;
    height          : 100vh;
    z-index         : 20;
    background-color: rgba(0, 0, 0, 0.75);
}

.modal {
    position        : fixed;
    top             : 20vh;
    left            : 5%;
    width           : 90%;
    background-color: rgba(216, 216, 216, 0.895);
    padding         : 1rem;
    border-radius   : 14px;
    box-shadow      : 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index         : 9990;
    animation       : slide-down 300ms ease-out forwards;
}

.notLoggedIn {
    display: flex;
    justify-content: space-between;
    p {
        font-size: 1.2rem;
        cursor: pointer;
        > * {
            color: rgb(255, 128, 0);
        }
    }
}

@media (min-width: 768px) {
    .modal {
        width: 40rem;
        left : calc(50% - 20rem);
    }
}

@keyframes slide-down {
    from {
        opacity  : 0;
        transform: translateY(-3rem);
    }

    to {
        opacity  : 1;
        transform: translateY(0);
    }
}