@media (max-width: 2800px) {
   
    .cart-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 2px solid #8a2b06;
        padding: 1rem 0;
        margin: 1rem 0;
    }
    
    .cart-item h2 {
        margin: 0 0 0.5rem 0;
        color: #363636;
    }
    
    .summary {
        width: 10rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    .price {
        font-weight: bold;
        color: #8a2b06;
    }
    
    
    .actions {
        display: flex;
        flex-direction: row;
        .amount {
            font-weight: bold;
            border: 1px solid #7d7d7d;
            padding: 0.4rem 0.75rem 0 0.75rem;
            border-radius: 6px;
            color: #363636;
            font-size: 1.2rem;
        }
    }
    
    
    .cart-item button {
        font: inherit;
        font-weight: bold;
        font-size: 1.25rem;
        color: #8a2b06;
        border: 1px solid #8a2b06;
        width: 3rem;
        text-align: center;
        border-radius: 6px;
        background-color: transparent;
        cursor: pointer;
        margin-left: 1rem;
        margin: 0.25rem;
    }
    
    .cart-item button:hover,
    .cart-item button:active {
        background-color: #8a2b06;
        color: white;
    }
}
@media (min-width: 768px) {
    .cart-item {
        .actions {
            flex-direction: row;
        }
    }
}

@media (max-width: 380px) {
    .cart-item {
        .actions {
            margin-right: 1rem;
            .amount {
                width: 2rem;
                font-size: 1rem;
                text-align: center;
                height: 2.3rem;
                padding: 0.4rem 0.2rem;
            }
            button {
                width: 2rem;
            }
        }
    }
}